import Swiper from 'swiper';
import { Navigation } from 'swiper/modules';

function gallery() {
  new Swiper('.gallery__slider', {
    modules: [Navigation],
    spaceBetween: 20,
    slidesPerView: 1,
    breakpoints: {
      768: {
        slidesPerView: 1.8
      },
      1320: {
        slidesPerView: 2.3,
      }
    },
    navigation: {
      nextEl: ".gallery .swiper-button-next",
      prevEl: ".gallery .swiper-button-prev",
    },
  });
}
gallery();