import { enablePageScroll, disablePageScroll } from "scroll-lock";

function popup() {
  let button = $('.vacancies__popup');
  let popup = $('.popup__bg');
  let close = $('.popup__close');
  let scrollableElement = document.querySelector('.popup__bg')
  button.on('click', function() {
    if (button.hasClass('active')) {
      popup.hide();
      enablePageScroll();
    } else {
      popup.show();
      disablePageScroll(scrollableElement);
    }
  });
  close.on('click', function() {
    popup.hide();
    enablePageScroll();
  });
  $(window).on('keydown', function(e) {
    if ( e.keyCode == 27 ) {
      popup.hide();
      enablePageScroll();
    }
  });
}
popup();