function vacancies() {
  let button = $('.vacancies__button');
  let container = $('.vacancies__container');
  for (let i = 0; i < Array.from(button).length; i++) {
    $(button[i]).on('click', function () {
      let id = button[i].getAttribute('data-id');
      let block = $('.vacancies__container[data-id="' + id + '"]');
      container.removeClass('active');
      button.removeClass('active');
      $(block).attr('data-id', $(this).attr('data-id')).addClass('active');
      $(this).addClass('active');
    });
  }
}
vacancies();

function vacanciesToggle() {
  let button = $('.vacancies__header__toggle');
  button.on('click', function() {
    $(this).parent().toggleClass('active');
    $(this).parent().next().toggleClass('active');
  });
}
vacanciesToggle();